.rating {
    grid-area: rating;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    justify-content: center;
    padding-right: 10px;
    padding-top: 20px;
}

.up,
.down {
    position: relative;
    width: 12px;
    height: 12px;
    border: 0;
    background-color: transparent;
    background-size: 100%;
    transition: background-color 0.36s ease-in-out;
}
.up:active,
.down:active {
    background-color: var(--orange);
}
.up {
    background-image: url('img/up.svg');
}

.down {
    background-image: url('img/down.svg');
}

.ups {
    /*font-weight: 700;*/
    /*font-size: 12px;*/
    /*line-height: 130%;*/
    color: var(--grey66);
}

@media (min-width: 580px) {

    .ups {
        /*font-size: 16px;*/
    }

    .up,
    .down {
        width: 24px;
        height: 24px;
        transition: outline-color 0.36s ease-in-out,
        background-color 0.36s ease-in-out;
        outline: 1px solid transparent;
        border-radius: 50%;
        outline-offset: 3px;
    }

    .up:hover,
    .down:hover {
        outline-color: var(--orange);
    }

    .rating {
        padding-top: 0;
        padding-bottom: 10px;
        flex-direction: row;
        justify-content: flex-start;
    }
}
