.content {
    grid-area: content;
    padding-top: 10px;
    padding-right: 10px;
    margin-bottom: 10px;
}

.linkPost {
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    font-weight: 700;
    /*font-size: 18px;*/
    /*line-height: 130%;*/
    /*color: var(--black);*/
    margin-bottom: 10px;
    transition: color 0.36s ease-in-out;
}

.linkPost:after {
    content: '';
    position: absolute;
    inset: 0;
    border-radius: 20px;
    transition: box-shadow 0.36s ease-in-out;
}

.linkAuthor {
    position: relative;
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    width: min-content;
    /*font-size: 12px;*/
    /*line-height: 14px;*/
    /*color: var(--orange);*/
    transition: color 0.36s ease-in-out;
}

.linkAuthor:hover {
    color: var(--black);
}

@media (min-width: 580px) {
    .linkPost {
        /*font-size: 26px;*/
    }
    .linkPost:hover {
        color: var(--orange);
    }

    .linkPost:hover:after {
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
    }

    .linkAuthor {
        /*font-size: 14px;*/
    }
}

@media (min-width: 768px) {
    .linkPost {
        /*font-size: 32px;*/
    }
}
