.delete {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15px;
    height: 15px;
    border: 0;
    background-color: transparent;
    color: var(--grey99);
    transition: color .36s ease-in-out;
    outline: 2px solid transparent;
    border-radius: 6px;
}

.delete:hover {
    color: var(--black);
}

.delete:focus {
    outline-color:var(--black);
}

.delete:active {
    color: var(--orange);
}

.delete svg {
    width: 100%;
    height: 100%;
}

@media (min-width: 580px) {
    .delete {
        top: 15px;
        right: 15px;
        width: 25px;
        height: 25px;
    }
}
